
.contain {
    width: 100%;
    border: solid black;
    margin-top: 1em;
  
  }
  
  .container {
    width: auto;
    text-align: center;
    margin: 1em;
    margin-top: 0;
  }
 
  .cont img {
    width: 100%;
  }

.pieges ul {
    padding: 0;
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: space-around;
  }
  
  .pieges ul li {
    border: solid black;
    border-radius: 1em;
    margin: 1em;
    width: 30em;
    background-color: floralwhite;
  }
  
  .pieges ul li img {
    width: 90%;
    border: solid black;
    border-radius: 1em;
    margin: auto;
  }
  .pieges ul {
    list-style: none;
  }
  @media (max-width: 1024px) {
    .pieges .item {
      width: 90vw;
    }
  }